import React from 'react';

const styles = {
  container: {
    backgroundColor: '#f1f3f6',
    minHeight: '100vh',
    padding: '30px',
    display: 'flex',
    justifyContent: 'center',
  },
  content: {
    display: 'flex',
    width: '90%',
    maxWidth: '1200px',
    gap: '20px',
  },
  leftSection: {
    flex: 3,
    backgroundColor: '#fff',
    padding: '20px',
    borderRadius: '5px',
  },
  item: {
    display: 'flex',
    borderBottom: '1px solid #e0e0e0',
    padding: '20px 0',
  },
  itemImage: {
    width: '120px',
    height: '120px',
    objectFit: 'contain',
    marginRight: '20px',
  },
  itemDetails: {
    flex: 1,
  },
  itemTitle: {
    fontWeight: 500,
    marginBottom: '5px',
  },
  itemSub: {
    fontSize: '14px',
    color: '#757575',
    marginBottom: '5px',
  },
  outOfStock: {
    color: '#ff6161',
    fontWeight: 'bold',
    marginBottom: '10px',
  },
  actionRow: {
    display: 'flex',
    gap: '20px',
    fontSize: '14px',
    fontWeight: 'bold',
    color: '#212121',
  },
  rightSection: {
    flex: 1,
    backgroundColor: '#fff',
    padding: '20px',
    height: 'fit-content',
    borderRadius: '5px',
  },
  sectionTitle: {
    fontWeight: 'bold',
    borderBottom: '1px solid #f0f0f0',
    paddingBottom: '10px',
    marginBottom: '10px',
  },
  priceRow: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: '10px',
    fontSize: '14px',
  },
  totalAmount: {
    fontWeight: 'bold',
    fontSize: '16px',
  },
  savingText: {
    color: 'green',
    fontWeight: 500,
    fontSize: '14px',
    marginTop: '10px',
  },
  placeOrderBtn: {
    marginTop: '20px',
    backgroundColor: '#fb641b',
    color: '#fff',
    border: 'none',
    padding: '14px',
    width: '100%',
    fontSize: '16px',
    fontWeight: 'bold',
    cursor: 'pointer',
  },
};

const CheckoutPage = () => {
  const cartItems = [
    {
      id: 1,
      title: 'RED TAPE Men Slides',
      subtitle: 'Size: 10, Blue, 10',
      image:
        'https://rukminim2.flixcart.com/image/312/312/xif0q/slipper-flip-flop/d/4/b/-original-imagt7ubxyjyhzpb.jpeg',
    },
    {
      id: 2,
      title: "Don't Sweat the Small Stuff",
      subtitle: 'English, Paperback, Carlson Richard',
      image:
        'https://rukminim2.flixcart.com/image/312/312/kj7gwi80-0/book/z/r/3/don-t-sweat-the-small-stuff-original-imafyhnkgqjxnk4k.jpeg',
    },
    {
      id: 3,
      title: 'Tarkash',
      subtitle: 'Hindi, Hardcover, Akhtar Javed',
      image:
        'https://rukminim2.flixcart.com/image/612/612/kqgyhe80/book/e/6/w/tarkash-original-imag4g2ydfd5hgmc.jpeg',
    },
  ];

  

  return (
    <div style={styles.container}>
      <div style={styles.content}>
        {/* Left - Cart Items */}
        <div style={styles.leftSection}>
          <button style={{ float: 'right', padding: '6px 12px', fontWeight: 'bold' }}>
            Enter Delivery Pincode
          </button>
          <h3 style={{ marginBottom: '20px' }}>From Saved Addresses</h3>

          {cartItems.map((item) => (
            <div key={item.id} style={styles.item}>
              <img src={item.image} alt={item.title} style={styles.itemImage} />
              <div style={styles.itemDetails}>
                <div style={styles.itemTitle}>{item.title}</div>
                <div style={styles.itemSub}>{item.subtitle}</div>
                <div style={styles.outOfStock}>Out Of Stock</div>
                <div style={styles.actionRow}>
                  <span>SAVE FOR LATER</span>
                  <span>REMOVE</span>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Right - Price Summary */}
        <div style={styles.rightSection}>
          <div style={styles.sectionTitle}>PRICE DETAILS</div>
          <div style={styles.priceRow}>
            <span>Price (4 items)</span>
            <span>₹12,797</span>
          </div>
          <div style={styles.priceRow}>
            <span>Discount</span>
            <span style={{ color: 'green' }}>− ₹8,653</span>
          </div>
          <div style={styles.priceRow}>
            <span>Coupons for you</span>
            <span style={{ color: 'green' }}>− ₹75</span>
          </div>
          <hr />
          <div style={{ ...styles.priceRow, ...styles.totalAmount }}>
            <span>Total Amount</span>
            <span>₹4,069</span>
          </div>
          <div style={styles.savingText}>You will save ₹8,728 on this order</div>

          <button style={styles.placeOrderBtn}>PLACE ORDER</button>
        </div>
      </div>
    </div>
  );
};

export default CheckoutPage;

